.wp-block-accordion-panel-accordion-panel{
	flex-grow: 0;
	display: flex;
	--transTime:0.5s;
	--transEase:cubic-bezier(.72,.72,.01,.99);
	transition:flex-grow var(--transTime) var(--transEase);
	position: relative;
	align-self: stretch;
	overflow: hidden;
}
.is-vertical>.wp-block-accordion-panel-accordion-panel{
	flex-direction: column;
}
.wp-block-accordion-panel-accordion-panel:is(
	.is-panel-open, .is-selected, :has(.is-selected)
){
	flex-grow: 1;
}

