// Panel Header

.wp-block-accordion-panel-accordion-panel .panel-header{
	text-decoration: none;
	overflow: hidden;
}
:not(.is-vertical)>.wp-block-accordion-panel-accordion-panel .panel-header{
	writing-mode: vertical-lr;
	text-orientation: mixed;
}

