// Panel

.wp-block-accordion-panel-accordion-panel .panel{
	width: 0px;
	overflow: hidden;
	z-index: 2;
	transition: var(--transTime) var(--transEase);
	background-color: var(--panelBgColor);
}
.wp-block-accordion-panel-accordion-panel.is-panel-open .panel{
	flex-grow: 1;
}

// Panel Content

.wp-block-accordion-panel-accordion-panel .panel-content{

	align-self: stretch;
	width: var(--availContentW);

}
.wp-block-accordion-panel-accordion-panel.is-panel-open .panel-content{
	display: block;
	flex-grow: 1;
}

// Accordion Rule

.wp-block-accordion-panel-accordion-panel *:has(.accordionRule){
	position: relative;
}
.wp-block-accordion-panel-accordion-panel .accordionRule{
	--ruleWidth:2px;
	position: absolute;
	background-color: var(--ruleColor);
	display: block;
}

.wp-block-accordion-panel-accordion-panel .accordionRule:is(
	.accordionRule-left, .accordionRule-right
){
	top: 0px;
	height: 100%;
	width: var(--ruleWidth);
}
.accordionRule.accordionRule-left{
	left: 0px;
	transform: translate(-50%);
}
.wp-block-accordion-panel-accordion-panel:last-child .accordionRule-right{
	right: 0%;
	transform: translate(50%);
}


